import * as actionType from './actionType'
import {getData} from 'util'
import {API_DELIVERYSETUP_SEARCH, API_DELIVERYSETUP_CREATE, API_DELIVERYSETUP_UPDATE} from 'api'
//同步
export const update = (object) => ({
  type: actionType.DELIVERYSETUP_UPDATE,
  data: object
})

//异步

// 获取列表
export const handleGetList = (reqData) => (dispatch) => {
  dispatch({type: actionType.DELIVERYSETUP_UPDATE, data: {tableLoading: true}})
  getData({api: API_DELIVERYSETUP_SEARCH, ...reqData}).then(data => {
    // 相应的逻辑判断
    let {page, size} = reqData.searchParams
    console.log(size)
    dispatch({
      type: actionType.DELIVERYSETUP_UPDATE,
      data: {
        page,
        size,
        tableLoading: false,
        total: data.result.total,
        searchResult: data.result.list || []
      }
    })
  }).catch(err => {
    dispatch({
      type: actionType.DELIVERYSETUP_FILE
    })
  })
}

// 添加/编辑
export const handleAddOrEdit = (reqData, callBack) => (dispatch) => {
  dispatch({type: actionType.DELIVERYSETUP_UPDATE, data: {modalTuttonLoading: true}})
  let api = reqData.data && reqData.data.id ? API_DELIVERYSETUP_UPDATE : API_DELIVERYSETUP_CREATE
  getData({api, ...reqData}).then(data => {
    // 相应的逻辑判断
    dispatch({
      type: actionType.DELIVERYSETUP_UPDATE,
      data: {
        modalTuttonLoading: false,
        isShowModal: false
      }
    })
    callBack&&callBack()
  }).catch(err => {
    dispatch({
      type: actionType.DELIVERYSETUP_FILE
    })
  })
}



