import React, {PureComponent} from 'react'
import {
  Table,
  Modal,
  message
} from 'antd'
const confirm = Modal.confirm
import {connect} from 'react-redux'
import {mapDispatchToProps} from '../redux/mapDispatchToProps'

@connect(
  state => ({...state.deliverySetUp, userLoation: state.userLoation}),
  mapDispatchToProps
)
export default class app extends PureComponent {
  constructor() {
    super()
    this.state = {
    }
    this.columns = [{
      title: '操作',
      width: 100,
      dataIndex: '',
      key: '',
      render: (text, record, index) => <div className="tableOperation">
        <span className="blue" onClick={() => {
          this.onEdit(text, record, index)
        }}>编辑</span>
      </div>
    }, {
      title: '快递代码',
      width: 150,
      dataIndex: 'expressCode',
      key: 'expressCode'
    }, {
      title: '快递名称',
      width: 150,
      dataIndex: 'expressName',
      key: 'expressName'
    }, {
      title: '是否需要账户',
      width: 150,
      dataIndex: 'demandAccountFlagDesc',
      key: 'demandAccountFlagDesc'
    }, {
      title: '商户号',
      width: 150,
      dataIndex: 'merchantAccount',
      key: 'merchantAccount'
    }, {
      title: '密码',
      width: 150,
      dataIndex: 'merchantPassword',
      key: 'merchantPassword'
    }, {
      title: '模板编码',
      width: 100,
      dataIndex: 'expressTemplate',
      key: 'expressTemplate'
    }]
  }

  onEdit(text, record, index) {
    const {update} = this.props
    update({isShowModal: true, modalTitle: '快递配置', modalData: record})
  }
  componentDidMount() {
    const {handleGetList, searchParams, page, size, userLoation} = this.props
    if (!userLoation || !userLoation.value ) return
    handleGetList({searchParams: {page, size, ...searchParams}})
  }
  // 监听下拉仓库改变了重新获取数据
  componentWillReceiveProps(newProps, newStates) {
    const {handleGetList, searchParams, page, size, userLoation} = this.props
    if ( newProps.userLoation.value != userLoation.value ) {
      handleGetList({searchParams: {page: 1, size: newProps.size, ...newProps.searchParams}})
    }
  }
  // 排序 columns排序列
  // isAsc 是否升序 true, false
  onChange =  (pagination, filters, sorter, extra) => {
    const {handleGetList, searchParams, page, size} = this.props
    if (sorter.order) {
      if (sorter.order === 'ascend') {
        handleGetList({searchParams: {page, size, ...searchParams, isAsc: true, columns: sorter.field}})
      } else {
        handleGetList({searchParams: {page, size, ...searchParams, isAsc: false, columns: sorter.field}})
      }
    } else {
      // 判断是点跳页面还是排序
      handleGetList({searchParams: {page: pagination.current, size: pagination.pageSize, ...searchParams}})
    }
  }
  render() {
    const {
      total,
      page,
      size,
      tableLoading,
      searchResult
    } = this.props
    let pagination = {
      total,
      showTotal: (total, range) => `共 ${total} 条`, // 显示总数
      showQuickJumper: true, // 是否可以快速跳转至某页
      current: page,
      pageSize: size,
      showSizeChanger: true, // 是否可以改变 pageSize
      className: 'pagination',
      pageSizeOptions: ['50', '100', '200', '500']
    }
    return (
      <div className='table-group'>
        <Table
          rowKey={record => record.id}
          loading={tableLoading}
          columns={this.columns}
          dataSource={searchResult}
          scroll={{x: 1050, y: 400}}
          onChange={this.onChange}
          pagination={pagination}
        />
      </div>
    )
  }
}



