import React, {PureComponent} from 'react'
import {
  Modal,
  Row,
  Col,
  Input,
  Form,
  message,
  Button,
  Radio
} from 'antd'
import {popupFormItemLayout} from 'enum'
import {CsxFormSelect} from 'Form'
import {API_DELIVERYSETUP_GETADDR} from 'api'
import {connect} from 'react-redux'
import {mapDispatchToProps} from '../redux/mapDispatchToProps'
const FormItem = Form.Item
const RadioGroup = Radio.Group

@connect(
  state => ({...state.deliverySetUp}),
  mapDispatchToProps
)
class App extends PureComponent {
  constructor() {
    super()
    this.state = {
    }
  }
  componentDidMount() {
    // 数据初始化
    const {modalData, update, expects} = this.props
    // 判断是新增还是编辑
    if (modalData) {
      let reqObj = {...modalData}
      // 特殊处理数据
      reqObj.expressName = {
        value: reqObj.expressCode && reqObj.expressCode.toString(),
        children: reqObj.expressName && reqObj.expressName.toString()
      }
      if (modalData.demandAccountFlag === 0) {
        update({expects: true})
      }
      update({modalData: {...reqObj}})
    }
  }
  handleSubmit = (e) => {
    const {form, modalData, handleAddOrEdit} = this.props
    e.preventDefault()
    form.validateFieldsAndScroll((err, values, expressNameShow) => {
      if (!err) {
        let reqObj = {...values}
        for (let i in reqObj) {
          if (Object.prototype.toString.call(reqObj[i]) === '[object Object]') {
            // 特殊处理关联地点
            if (i == 'expressName') {
              expressNameShow = reqObj.expressName.children
              reqObj.expressCode = reqObj.expressName.value
              reqObj.expressName = expressNameShow.replace(reqObj.expressName.value, '')
              // continue
            }
          }
        }
        let result = {
            data: reqObj
          },
          msg = '创建成功'
        // 判断是否是修改,加ID
        if (modalData && modalData.id) {
          result.data.id = modalData.id
          result.urlParams = [modalData.id]
          msg = '编辑成功'
        }
        handleAddOrEdit({...result}, () => {
          message.success(msg)
          const {handleGetList, searchParams, page, size} = this.props
          handleGetList({searchParams: {page, size, ...searchParams}})
        })
      }
    })
  }
  // 关闭弹窗
  handleCloseModal = () => {
    const {update} = this.props
    update({isShowModal: false, expects: false})
  }

  handleChangeRadio = (e) => {
    const {update, modalData} = this.props
    if (e.target.value === 0) {
      this.props.form.setFieldsValue({merchantAccount: ' ', merchantPassword: ' ', expressTemplate: ' '})
      // this.props.form.resetFields(['merchantAccount', 'merchantPassword', 'expressTemplate'])
      this.forceUpdate()
      update({expects: true})
    } else {
      this.props.form.setFieldsValue({merchantAccount: ''})
      update({expects: false})
    }
  }
  render() {
    const {modalTitle, form: {getFieldDecorator}, modalTuttonLoading, modalData, showExp, expects} = this.props
    return (
      <Modal
        title={modalTitle}
        visible={true}
        onCancel={this.handleCloseModal}
        mask={false}
        className='operationModal'
        footer={null}
      >
        <Form onSubmit={this.handleSubmit} autoComplete='off'>
          <FormItem
            {...popupFormItemLayout}
            label="快递选择"
          >
            {getFieldDecorator('expressName', {
              initialValue: modalData.expressName,
              rules: [{
                required: true, message: '请填写!'
              }]
            })(
              <CsxFormSelect
                dateApi={API_DELIVERYSETUP_GETADDR}
                selectItem={{key: 'key', valueKey: ['key', 'value']}}
              />
            )}
          </FormItem>
          <FormItem
            {...popupFormItemLayout}
            label="需要开通账号"
          >
            {getFieldDecorator('demandAccountFlag', {
              initialValue: modalData.demandAccountFlag,
              rules: [{
                required: true, message: '请选择!'
              }]
            })(
              <RadioGroup
                onChange = {this.handleChangeRadio}>
                <Radio value={1}>是</Radio>
                <Radio value={0}>否</Radio>
              </RadioGroup>
            )}
          </FormItem>
          <FormItem
            {...popupFormItemLayout}
            label="商户号"
            // hasFeedback
          >
            {getFieldDecorator('merchantAccount', {
              initialValue: modalData.merchantAccount,
              rules: [{
                required: !expects, message: '请填写!'
              }]
            })(
              <Input disabled={expects} />
            )}
          </FormItem>
          <FormItem
            {...popupFormItemLayout}
            label="密码"
          >
            {getFieldDecorator('merchantPassword', {
              initialValue: modalData.merchantPassword
            })(
              <Input disabled={expects}/>
            )}
          </FormItem>

          <FormItem
            {...popupFormItemLayout}
            label="模板规格"
          >
            {getFieldDecorator('expressTemplate', {
              initialValue: modalData.expressTemplate
            })(
              <Input disabled={expects} />
            )}
          </FormItem>
          <Row
            type="flex"
            justify="center"
          >
            <Col>
              <Button style={{marginRight: '48px'}} onClick={this.handleCloseModal}>取消</Button>
              <Button type="primary" htmlType="submit" loading={modalTuttonLoading}>保存</Button>
            </Col>
          </Row>
        </Form>
      </Modal>
    )
  }
}
export default Form.create()(App)



